.clearIcon:hover {
  color: red;
}

.clearIcon {
  margin-left: -30px;
  margin-right: 30px;
}

.li {
  font-size: 19px;
}

.list {
  display: flex;
  margin-top: 10px;
}

.main {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 100px;
  /* justify-content: center; */
  align-items: center;
  /* box-shadow: 5px 5px 15px -5px black; */
  background-color: rgb(68, 189, 148);
}

.content {
  width: 312px;
  height: 65vh;
  box-shadow: 5px 5px 15px -5px black;
  background: none;
  background-color: aliceblue;
  border-radius: 15px;
}

.head {
  background-color: rgb(133, 91, 172);
  color: white;
  box-shadow: 5px 5px 15px -5px black;
  text-align: center;
  margin: 14px 0;
}

.input {
  display: flex;
  justify-content: center;
  margin: 45px;
}

input {
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 200px;
  border-bottom: 3px solid rgb(133, 91, 172);
  margin: 0 10px;
  outline: none;
}

.pbut:hover {
  color: green;
}

.sbut {
  background-color: DodgerBlue;
  border: none;
  color: white;
  padding: 12px 16px;
  font-size: 16px;
  cursor: pointer;
}
